body{
  font-family:Arial, Helvetica,sans-serif;
  min-height:96vh;
}

.container{
  margin:20px auto;
  overflow:hidden;
  width:80%;
  text-align:center;
}

.apexcharts-selection-rect{
  color:red;
}

.inputs-container{
  display:flex;
  justify-content:space-around;
}

.select-container{
  display:flex;
  width:50%;
  margin-top:15px;
  justify-content:center;
  align-items:center; 
  margin-left:auto;
  margin-right:auto;
}

.text-container button{
  margin:15px;
  width:30%;
}

.select-label{
  width:30%;
}

.form-select:disabled{
  background-color:#250d49 !important;
  color:#6c757d !important;
}

@media(max-width:800px){
  .inputs-container{
    flex-direction:column;
    align-items:center;
  }
  .text-container button{
    width:80%;
  }
  .select-container{
    width:80%;
    flex-direction:column;
  }
}